 .navbar{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    width: 100%;
    height: 5vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    background-color:whitesmoke;
    
 }

 .navbar_right{
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
 }
 .navbar_right div{
    margin: 0 0.5vmax;
 }
